.default-search.form-group {
  margin-bottom: 0px;
  margin-top: 20px; }

.default-search-options {
  padding-bottom: 1em; }
  .default-search-options .glyphicon {
    margin-left: 1em; }

#search-category {
  width: 100px;
  outline: none;
  color: #000; }
  #search-category:active {
    padding-bottom: 5px; }

#search-button {
  width: 100px;
  outline: none;
  color: #000;
  background-color: #FFCD00; }
  #search-button:hover {
    background-color: #FBD01B; }

@media (max-width: 992px) {
  .default-search-container {
    text-align: center; } }



